.productReviewsContainer {
    width: 100%;
    box-sizing: border-box;
    /* background-color: rgb(108, 107, 107); */
    background-image: url("https://images.theconversation.com/files/286560/original/file-20190801-169706-htdqem.jpg?ixlib=rb-1.1.0&rect=0%2C269%2C6000%2C3000&q=45&auto=format&w=1356&h=668&fit=crop");
    border-left: 1px solid rgba(0, 0, 0, 0.158);
    height: 100vh;
  }
  
  .productReviewsForm {
    width: 22rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 3vmax;
    background-color: rgb(174, 169, 169);
    background-repeat: no-repeat;
    background-size: contain;
  }
  
  .productReviewsFormHeading {
    color: rgba(0, 0, 0, 0.733);
    font: 300 2rem "Italic";
    text-align: center;
  }
  .productReviewsFormHeading1{
    color: rgba(251, 254, 253, 0.921);
    font: 900 2rem "Italic";
    text-align: center;
  }
  
  .productReviewsForm > div {
    display: flex;
    width: 100%;
    align-items: center;
    margin: 2rem;
  }
  .productReviewsForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 600 0.9vmax cursive;
    /* outline: none; */
  }
  
  .productReviewsForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(222, 184, 13, 0.623);
  }
  
  #createProductBtn{
    background-color: rgb(47, 116, 128);
  }

  @media screen and (max-width: 600px) {
    .productReviewsContainer {
      border-left: none;
      border-top: 1px solid rgba(0, 0, 0, 0.158);
    }
    .productReviewsForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .productReviewsForm > div > svg {
      font-size: 2.8vmax;
    }
  }