.productListContainer {
  width: 100%;
  box-sizing: border-box;
  background-color: rgb(139, 133, 133);
  border-left: 1px solid rgba(213, 33, 33, 0.158);
  display: flex;
  flex-direction: column;
  height: 100vh;
}

#productListHeading {
  font: 900 3rem "Roboto";
  padding: 0.5vmax;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.637);
  transition: all 0.5s;
  margin: 2rem;
  text-align: center;
}

.productListTable {
  background-color: rgb(190, 190, 190);
  border: none !important;
}

.productListTable div {
  font: 600 1vmax "Roboto";
  color: rgba(38, 38, 37, 0.678);
  border: none !important;
}

.productListTable a,
.productListTable button {
  color: rgba(0, 0, 0, 0.88);
  transition: all 0.5s;
}

.productListTable a:hover {
  color: tomato;
}

.productListTable button:hover {
  color: rgb(236, 30, 30);
}

.MuiDataGrid-columnHeader div {
  color: rgb(255, 250, 250);
  background-color: gray;
  border-radius: 20px 20px 10px 10px;
}

@media screen and (max-width: 600px) {
  .productListTable div {
    font: 300 4vw "Roboto";
  }
}