.paymentContainer{
    display: grid;
    place-items: center;
    /* background-color: rgb(168, 132, 132); */
    height: 75vh;
    margin: 2vmax;
    background-image: url("https://png.pngtree.com/thumb_back/fw800/back_our/20190625/ourmid/pngtree-blue-business-technology-information-security-banner-background-image_260807.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}
.paymentForm{
    width: 22%;
    height: 100%;
}
.paymentForm>p{
    font: 400 2vmax "Roboto";
    color: rgba(0,0,0,0.753);
    border-bottom: 6px solid rgba(126, 177, 16, 0.507);
    border-radius: 30px;
    box-shadow: 4px 4px 9px rgb(188, 214, 216);
    padding: 1vmax 0;
    text-align: center;
    width: 50%;
    margin: auto;
}

.paymentForm>div{
    display: flex;
    align-items: center;
    margin: 2vmax 0;
    border: 2px solid rgb(54, 232, 208);
    border-radius: 15px;
}
.paymentInput{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    border-radius: 4px;
    outline: none;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(131, 19, 19, 0.267);
}
.paymentForm>div{
  background-color: rgb(216, 213, 213);
}
.paymentForm>div>svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623);
}
.paymentFormBtn{
    border: none;
    background-color: rgb(46, 186, 55);
    border-radius: 10px;
    color: rgb(10, 10, 10);
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 1s;
    outline: none;
}
.paymentFormBtn:hover{
    background-color: rgb(209, 55, 28);
    color: whitesmoke;
}

@media screen and (max-width: 600px) {
    .paymentForm {
      width: 90%;
    }
  
    .paymentForm > p {
      font: 400 8vw "Roboto";
      padding: 4vw 0;
      width: 60%;
    }
  
    .paymentForm > div {
      margin: 10vw 0;
    }
  
    .paymentInput {
      padding: 4vw 10vw;
    }
  
    .paymentForm > div > svg {
      font-size: 6vw;
    }
  
    .paymentFormBtn {
      font: 300 4vw "Roboto";
      padding: 4vw;
    }
  }