.shippingContainer {
    width: 100vw;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(#d89d9d, #9198e5);
  }
  
  .shippingBox {
    /* background-color: rgb(128, 17, 17); */
    background: linear-gradient(#078bd1, #767679);
    width: 25vw;
    height: 90vh;
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 10px 10px 10px rgb(117, 63, 87);
  }
  
  .shippingHeading {
    text-align: center;
    color: rgba(10, 10, 12, 0.664);
    font: 900 1.3vmax "Roboto";
    letter-spacing: 3px;
    padding: 1.3vmax;
    border-bottom: 7px solid rgba(0, 0, 0, 0.205);
    border-radius: 30px;
    width: 50%;
    margin: auto;
  }
  
  .shippingForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 80%;
    transition: all 0.5s;
  }
  
  .shippingForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  
  .shippingForm > div > input,
  .shippingForm > div > select {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 3px solid rgba(91, 48, 177, 0.267);
    border-radius: 20px;
    font: 400 1vmax cursive;
    outline: none;
    font-family:'Times New Roman', Times, serif;
    
  }
  
  .shippingForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
  }
  
  .shippingBtn {
    border: none;
    background-color: rgb(31, 171, 167);
    color: white;
    font: 300 1vmax "Roboto";
    width: 100%;
    padding: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
    margin: 2vmax;
  }
  
  .shippingBtn:hover {
    background-color: rgb(60, 144, 165);
  }
  
  @media screen and (max-width: 600px) {
    .shippingBox {
      width: 100vw;
      height: 95vh;
    }
  
    .shippingHeading {
      font: 400 6vw "Roboto";
      padding: 5vw;
    }
  
    .shippingForm {
      padding: 11vw;
    }
  
    .shippingForm > div > input,
    .shippingForm > div > select {
      padding: 5vw 10vw;
      font: 300 4vw cursive;
    }
  
    .shippingForm > div > svg {
      font-size: 6vw;
      transform: translateX(3vw);
    }
  
    .shippingBtn {
      font: 300 4vw "Roboto";
      padding: 4vw;
    }
  }