footer{
    margin-top: 2vmax;
    padding: 2vmax;
    /* background-color: rgb(34,33,33); */
    background:linear-gradient(rgba(193, 196, 191, 0), rgb(9, 9, 9));
    color: rgb(244, 33, 33);
    display: flex;
    align-items: center;
}

.leftFooter{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: top;
}
.leftFooter>img{
    border: 20px solid rgb(124, 121, 121);
    border-radius: 30px;
    box-shadow: 10px 10px 120px rgb(12, 11, 11);
}
.leftFooter>img{
    width: 10vmax;
    margin: 1vmax;
    cursor: pointer;
}
.midFooter{
    width: 60%;
    text-align: center;
}
.midFooter>h1{
    font-size: 3vmax;
    word-spacing: 10%;
    letter-spacing: 4px;
    padding: 30px;
    color: rgb(7, 7, 6);
}
.midFooter>p{
    max-width: 60%;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 1vmax auto;
    color: rgb(255, 252, 252);
}
.rightFooter{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.rightFooter>h4{
    font-family: "Roboto";
    font-size: 1.9vmax;
    text-decoration:underline;
    color: black;
}
.rightFooter>a{
    text-decoration: none;
    font-size:1.4vmax;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: rgb(253, 253, 253);
    transition: all 0.5;
    margin: 0.5vmax;
    font-weight:600;
    letter-spacing: 2px;
}
.rightFooter>a:hover{
    color: #0a0101;
}

@media screen and (max-width:600px){
    footer{
        margin-bottom: 16vmax;
        padding: 1vmax;
        background:linear-gradient(rgba(193, 196, 191, 0), rgb(9, 9, 9));
        color: rgb(244, 33, 33);
        display: flex;
        align-items: center;
    }
    
    .leftFooter{
        width: 20%;
        display: flex;
        flex-direction: column;
        align-items: top;
    }
    .leftFooter>img{
        border: 12px solid rgb(124, 121, 121);
        border-radius: 20px;
        box-shadow: 10px 10px 120px rgb(12, 11, 11);
        width: 10vmax;
        margin: 1vmax;
        cursor: pointer;
    }
    .midFooter{
        width: 60%;
        text-align: center;
        margin-left:27px;
    }
    .midFooter>h1{
        font-size: 2vmax;
        padding: 20px;
        letter-spacing: 1px;
        color: rgb(7, 7, 6);
    }
    .midFooter>p{
        /* max-width: 40%; */
        padding: 2px;
        display: flex;
        justify-content: center;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        /* margin: 3vmax auto; */
        color: rgb(255, 252, 252);
    }
    .rightFooter{
        width: 20%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .rightFooter>h4{
        font-family: "Roboto";
        font-size: 2vmax;
        text-decoration:underline;
        color: black;
    }
    .rightFooter>a{
        text-decoration: none;
        font-size:1.5vmax;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        color: rgb(253, 253, 253);
        transition: all 0.5;
        margin: 0.5vmax;
        font-weight:600;
        letter-spacing: 2px;
    }
    .rightFooter>a:hover{
        color: #0a0101;
    }
}