.navbar-brand > img {
    height: 8vh;
    visibility: inherit;
    border-radius: 40px 50px 10px 30px;
  }

.allback{
    background-repeat: no-repeat;
    background-position: top;
    background-size: 16%;
    display: flex;
    justify-content: flex-start;
    background-position: right;
    background:linear-gradient(rgba(168, 171, 167, 0), rgb(171, 171, 168));
    /* color: rgb(244, 33, 33); */
    color: rgb(207, 201, 201);

}

.navul{
    /* border: 20px solid pink; */
    display: flex;
    align-items: center;
    text-decoration: none;
    height: 20vh;    
    scroll-behavior: auto;
}
.navul>li{
    border: 2px solid pink;
    display: flex;
    align-items: center;
    text-decoration: none;
    height: 50px;
    margin: 1px;
}
.navul>li:hover{
    border: 10px solid salmon;
}

li>a{
    display: inline;
    text-decoration: none;
    color: rgb(103, 112, 7);
    padding: 20%;
}
li>a>svg{
    color: rgb(122, 134, 15);
}
.speedDial{
    position: fixed;
    right: 2vmax;
    top: 1vmax;
}
.button{
    position: fixed;
    right: 1vmax;
    bottom: 1vmax;
    width: 56px;
    height: 56px;
    border-radius: 100%;
    color: red;
}
.speedDialIcon{
    width: 66px;
    height: 56px;
    border-radius: 30%;
}
nav{
    height: 8vh;
    width: 90vw;
    display: flex;
    /* justify-content: center; */
    justify-content: space-between;
    align-items: center;
}
nav>img{
    height: 8vh;
}

main>{
    display: flex;
    text-decoration: none;

}
main>a{
    text-decoration: none;
}
main>a.first{
    color: brown;
}