.contactContainer {
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  display: grid;
  place-items: center;
  /* background-color: rgb(220, 28, 28); */
  position: fixed;
  background-image: url("https://t3.ftcdn.net/jpg/03/72/30/72/360_F_372307201_5bZ4I5abIGAHBS52fF8UGXkY6VVmBCl6.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.mailBtn {
  text-decoration: none;
  transform: translateX(-100vw);
  animation: mailBtnAnimation 2s forwards;
}

.mailBtn > button {
  text-decoration: none;
  font: 200 2vmax "Roboto";
  cursor: url("https://img.icons8.com/color/48/000000/edit--v2.png"), pointer;
  padding: 1vmax;
  display: flex;
  margin: 40px;
  border: 14px solid rgb(117, 113, 113);
  border-radius: 50px;
  background-color: rgb(207, 205, 205);
  box-shadow: 10px 10px 20px rgb(8, 7, 7);
}

@keyframes mailBtnAnimation {
  to {
    transform: translateX(0);
  }
}

@media screen and (max-width: 600px) {
  .contactContainer {
    /* background-image: url("https://t3.ftcdn.net/jpg/03/72/30/72/360_F_372307201_5bZ4I5abIGAHBS52fF8UGXkY6VVmBCl6.jpg"); */
    /* background: rgb(120, 116, 84); */
    background: linear-gradient(#b6b4b4, #303030);
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    display: grid;
    place-items: center;
    /* background-color: rgb(220, 28, 28); */
    position: fixed;
    background-repeat: no-repeat;
  }

  .mailBtn {
    text-decoration: none;
    transform: translateX(-100vw);
    animation: mailBtnAnimation 2s forwards;
  }

  .mailBtn > button {
    text-decoration: none;
    font: 200 2vmax "Roboto";
    cursor: url("https://img.icons8.com/color/48/000000/edit--v2.png"), pointer;
    padding: 2vmax;
    display: flex;
    margin: 40px;
    border: 14px solid rgb(117, 113, 113);
    border-radius: 50px;
    background-color: rgb(207, 205, 205);
    box-shadow: 10px 10px 20px rgb(8, 7, 7);
  }
}
