.orderSuccess {
    margin: auto;
    text-align: center;
    padding: 10vmax;
    height: 50vh;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    /* background-color: rgb(93, 135, 145); */
    background-image: url("https://st.depositphotos.com/1001069/4829/i/950/depositphotos_48294369-stock-photo-set-of-tools-on-wood.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
  .orderSuccess > svg {
    font-size: 7vmax;
    color: rgb(20, 175, 20);
  }
  .orderSuccess > p {
    font-size: 2vmax;
    margin: 20px;
  }
  .orderSuccess > a {
    background-color: rgb(101, 102, 102);
    color: rgb(248, 248, 249);
    /* border: none; */
    border: 2px solid rgb(203, 216, 13);
    box-shadow: 13px 13px 12px rgb(51, 50, 50);
    border-radius: 9px;
    border-top-color: red;
    border-left-color: aqua;
    border-right-color: blue;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration: none;
    margin: 2vmax;
  }
  
  @media screen and (max-width: 600px) {
    .orderSuccess > a {
      padding: 3vw 6vw;
      font: 400 4vw "Roboto";
      margin: 2vmax;
    }
  
    .orderSuccess > svg {
      font-size: 20vw;
    }
    .orderSuccess > p {
      margin: 2vmax;
      font-size: 5vw;
    }
  }