.confirmOrderPage{
    height: 100vh;
    display: grid;
    grid-template-columns: 6fr 3fr;
    background: linear-gradient(217deg, rgba(142, 215, 227, 0.8), rgba(255,0,0,0) 70.71%),linear-gradient(127deg, rgba(192, 245, 192, 0.8), rgba(0,255,0,0) 70.71%),linear-gradient(336deg, rgba(141, 120, 78, 0.8), rgba(0,0,255,0) 70.71%);
}
.confirmOrderPage>div:last-child{
    border-left: 4px solid rgba(0,0,0,0.247);
    background: linear-gradient(217deg, rgba(27, 126, 141, 0.8), rgba(255,0,0,0) 70.71%),linear-gradient(127deg, rgba(57, 190, 57, 0.8), rgba(0,255,0,0) 70.71%),linear-gradient(336deg, rgba(162, 122, 42, 0.8), rgba(0,0,255,0) 70.71%);

}
.confirmshippingArea{
    padding: 5vmax;
    padding-bottom: 0%;
}
.confirmshippingArea>p{
    font: 100 1.7vmax "Roboto";
    border: 10px solid rgb(83, 130, 144);
    text-align: center;
    border-radius:60rem;
}
.confirmshippingAreaBox,
.confirmCartItemsContainer{
    margin: 2vmax;
}
.confirmshippingAreaBox>div{
    display: flex;
    margin: 2vmax 0;
}

.confirmshippingAreaBox>div>p{
    font: 900 1vmax "Roboto";
    color: rgb(5, 4, 4);
}
.confirmshippingAreaBox>div>span{
    margin: 0 1vmax;
    font: 600 1vmax "Roboto";
    color: #575757;
}

.confirmCartItems > p {
  font: 100 1.7vmax "Roboto";
  border: 10px solid rgb(83, 130, 144);
  text-align: center;
  border-radius:60rem;
}

.confirmCartItems{
    padding: 5vmax;
    padding-top: 2vmax;
}

.confirmCartItemsContainer{
    max-height: 20vmax;
    overflow-y: auto;
}
.confirmCartItemsContainer>div{
    display: flex;
    font: 400 1vmax "Roboto";
    align-items: center;
    justify-content: space-between;
    margin: 2vmax 0;
}
.confirmCartItemsContainer>div>img{
    width: 3vmax;
}
.confirmCartItemsContainer>div>a{
    text-decoration: none;
    width: 60%;
    font-size: large;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-style: italic;
    font-variant: small-caps;
    margin: 0 2vmax;
    color: #575757;
}
.confirmCartItemsContainer>div>span{
    font: 100 1vmax "Roboto";
    color: #5e5e5e;
}

.orderSummary{
    padding: 7vmax;
}
.orderSummary>p{
    text-align: center;
    font: 400 1.8vmax "Roboto";
    border-bottom: 1px solid rgba(0,0,0,0.267);
    padding: 1vmax;
    width: 100%;
    margin: auto;
    box-sizing: border-box;
}
.orderSummary>div>div{
    display: flex;
    font: 300 1vmax "Roboto";
    justify-content: space-between;
    margin: 2vmax 0;
}
.orderSummary>div>div>span{
    color: rgba(0,0,0,0.692);
}
.orderSummaryTotal{
    display: flex;
    font: 300 1vmax "Roboto";
    justify-content: space-between;
    border-top: 1px solid rgba(0,0,0,0.363);
    padding: 2vmax 0;
}
.orderSummary>button{
    background-color: rgb(37, 174, 201);
    color: rgb(215, 179, 71);
    width: 100%;
    padding: 1vmax;
    border: none;
    margin: auto;
    cursor: pointer;
    transition: 0.5s;
    font: 400 1vmax "Roboto";
    border-radius:1vmax;
}
.orderSummary>button:hover{
  background-color: rgb(21, 112, 148);
}


@media screen and (max-width: 600px) {
    .confirmOrderPage {
      grid-template-columns: 1fr;
      height: unset;
    }
  
    .confirmOrderPage > div:last-child {
      border-left: 0;
      border-top: 1px solid rgba(0, 0, 0, 0.247);
    }
  
    .confirmshippingArea > p {
      font: 400 6vw "Roboto";
    }
  
    .confirmshippingAreaBox > div {
      display: flex;
      margin: 6vw 0;
    }
  
    .confirmshippingAreaBox > div > p {
      font: 400 4vw "Roboto";
    }
    .confirmshippingAreaBox > div > span {
      font: 100 4vw "Roboto";
    }
  
    .confirmCartItems > p {
      font: 400 6vw "Roboto";
    }
  
    .confirmCartItemsContainer {
      max-height: 50vw;
    }
  
    .confirmCartItemsContainer > div {
      font: 400 4vw "Roboto";
      margin: 4vw 0;
    }
  
    .confirmCartItemsContainer > div > img {
      width: 10vw;
    }
  
    .confirmCartItemsContainer > div > a {
      margin: 0;
      width: 30%;
    }
  
    .confirmCartItemsContainer > div > span {
      font: 100 4vw "Roboto";
    }
  
    .orderSummary {
      padding: 12vw;
    }
  
    .orderSummary > p {
      font: 400 6vw "Roboto";
      padding: 4vw;
    }
  
    .orderSummary > div > div {
      font: 300 4vw "Roboto";
    }
  
    .orderSummaryTotal {
      font: 300 4vw "Roboto";
      padding: 5vw 0;
    }
  
    .orderSummary > button {
      padding: 4vw;
      margin: 4vw auto;
      font: 400 4vw "Roboto";
    }
  }