.aboutSection {
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    /* background-color: rgb(124, 121, 121); */
    background: linear-gradient(#e2aeae, #29b2be);
    display: grid;
    grid-template-columns: 4fr 4fr;
    position: fixed;
  }
  
  .aboutSectionGradient {
    background-image: linear-gradient(
      to bottom right,
      #e2aeae,
      #29b2be
    );
  }
  
  .aboutSectionContainer {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: rgb(50, 133, 159);
    width: 80vw;
    height: 80vh;
    transform: translateX(-50%) translateY(-50%);
    box-shadow: -10px 10px 10px rgba(0, 0, 0, 0.192);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .aboutSectionContainer > h1 {
    font: 400 3vmax "Roboto";
    color: rgb(7, 7, 7);
    margin: 2vmax;
  }
  
  .aboutSectionContainer > div {
    display: flex;
    width: 100%;
  }
  
  .aboutSectionContainer > div > div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2vmax;
    box-sizing: border-box;
  }
  
  .aboutSectionContainer > div > div > p {
    color: rgba(0, 0, 0, 0.623);
    font: 600 2vmax "Italic";
  }
  
  .aboutSectionContainer > div > div > button {
    margin: 1vmax 0;
  }
  
  .aboutSectionContainer > div > div > span {
    font: 100 1vmax "Roboto";
    color: rgba(0, 0, 0, 0.616);
    text-align: center;
    width: 80%;
  }
  
  .aboutSectionContainer2 {
    border-left: 4px solid rgba(231, 5, 5, 0.116);
  }
  
  .aboutSectionContainer2 > h2 {
    color: rgba(0, 0, 0, 0.623);
    font: 600 2vmax "Roboto";
    margin: 2vmax;
  }
  
  .aboutSectionContainer2 > a > svg {
    font-size: 4vmax;
  }
  
  .youtubeSvgIcon {
    color: tomato;
  }
  
  .instagramSvgIcon {
    color: rgb(218, 118, 218);
  }
  
  @media screen and (max-width: 600px) {
    .aboutSectionContainer > div {
      display: block;
    }
  
    .aboutSectionContainer > h1 {
      font: 400 5vmax "Roboto";
      margin: 3vmax;
    }
  
    .aboutSectionContainer > div > div > p {
      font: 100 2vmax "Roboto";
    }
  
    .aboutSectionContainer > div > div > span {
      font: 100 1.4vmax "Roboto";
      width: 70%;
    }
  
    .aboutSectionContainer2 {
      border-left: none;
    }
  
    .aboutSectionContainer2 > h2 {
      font: 100 3vmax "Roboto";
      margin: 2vmax;
    }
  
    .aboutSectionContainer2 > a > svg {
      font-size: 6vmax;
      margin: 1vmax;
    }
  }