.myOrdersPage {
    width: 100vw;
    max-width: 100%;
    padding: 0 7vmax;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: linear-gradient(#1e6eca, #272728);
  }
  
  #myOrdersHeading {
    text-align: center;
    font: 800 2.2vmax "Italic";
    letter-spacing: 5px;
    padding: 0.5vmax;
    box-sizing: border-box;
    color: rgb(4, 4, 4);
    transition: all 0.5s;
    background: linear-gradient(#1e6eca, #8e8e9a);
  }
  
  .myOrdersTable {
    background: linear-gradient(#1e6eca, #272728);
    box-shadow: 0px 2px 20px rgb(250, 248, 248);
  }
  
  .myOrdersTable div {
    font: 1000 1.1vmax "Roboto";
    font-style: italic;
    color: rgba(6, 6, 6, 0.678);
    border: none;
  }
  
  .myOrdersTable a {
    /* color: rgba(9, 9, 9, 0.527); */
    color: orangered;
    font: 900 1vmax "roboto";
    transition: all 0.5s;
  }
  
  .myOrdersTable a:hover {
    color: tomato;
  }
  
  .MuiDataGrid-columnHeader {
    background-color: rgb(42, 43, 42);
    padding: 1vmax !important;
  }
  
  .MuiDataGrid-columnHeader div {
    color: rgb(255, 255, 255);
    font: 500 1.1vmax "Roboto" !important;
  }
  
  .MuiDataGrid-iconSeparator {
    display: none !important;
  }
  
  @media screen and (max-width: 600px) {
    .myOrdersPage {
      padding: 0;
      height: 93vh;
    }
  
    #myOrdersHeading {
      font: 400 2.2vmax "Roboto";
      padding: 4vw;
    }
  
    .myOrdersTable div {
      font: 300 4vw "Roboto";
    }
  
    .MuiDataGrid-columnHeader {
      padding: 20px !important;
    }
  
    .MuiDataGrid-columnHeader div {
      font: 500 5vw "Roboto" !important;
    }
  }