.productsHeading {
    margin: 2vmax auto;
    width: 15vw;
    border-bottom: 8px solid rgba(249, 249, 6, 0.171);
    border-radius: 40px;
    padding: 2vmax;
    color: rgba(0, 0, 0, 0.678);
    font: 500 1.7vmax "Roboto";
    text-align: center;
    box-shadow: 4px 3px 10px rgb(32, 31, 31);
  }
  .productsHeading:hover{
    box-shadow: 4px 3px 10px silver ;
  }

  .product1{
    /* background: linear-gradient(217deg, rgba(167, 163, 52, 0.8), rgba(255,0,0,0) 70.71%),linear-gradient(127deg, rgba(179, 186, 47, 0.8), rgba(0,255,0,0) 70.71%),linear-gradient(336deg, rgba(151, 144, 17, 0.8), rgba(0,0,255,0) 70.71%); */
    height: 100vh;
    background-image: url("https://img.freepik.com/free-photo/variety-handy-tools-dark-background-labor-day-background-concept_1150-41808.jpg?w=1060&t=st=1677432699~exp=1677433299~hmac=4130695bd91044134c10c50bece42b507513900dcd869dfcface06e9096d007c");
    border: 15px solid silver;
    border-radius: 25px;
    /* background-repeat: no-repeat; */
    background-size: cover;
  }
  
  .products {
    display: flex;
    flex-wrap: wrap;
    padding: 0 5vmax;
    justify-content: center;
    min-height: 30vh;    
  }
  
  .paginationBox {
    display: flex;
    justify-content: center;
    margin: 6vmax;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    padding: 0;
  }
  
  .page-item {
    background-color: rgb(255, 255, 255);
    list-style: none;
    border: 1px solid rgba(0, 0, 0, 0.178);
    padding: 1vmax 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;
  }
  .page-item:first-child {
    border-radius: 5px 0 0 5px;
  }
  
  .page-item:last-child {
    border-radius: 0 5px 5px 0;
  }
  .page-link {
    text-decoration: none;
    font: 300 0.7vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
  }
  
  .page-item:hover {
    background-color: rgb(230, 230, 230);
  }
  
  .page-item:hover .page-link {
    color: rgb(0, 0, 0);
  }
  
  .pageItemActive {
    background-color: tomato;
  }
  
  .pageLinkActive {
    color: white;
  }
  
  .filterBox {
    width: 10vmax;
    position: absolute;
    padding: 30px;
    top: 10vmax;
    left: 4vmax;
    border: 7px solid rgb(73, 101, 8);
    border-radius: 60px;
    background-color: rgb(162, 171, 113);
    box-shadow: 10px 10px 10px rgb(14, 14, 14);
  }
  
  .categoryBox {
    padding: 0%;
  }
  
  .category-link {
    list-style: none;
    color: rgba(7, 7, 7, 0.61);
    font: 600 0.8vmax "Roboto";
    margin: 0.4vmax;
    cursor: pointer;
    transition: all 0.5s;
  }
  .category-link:hover {
    color: rgb(2, 2, 2);
  }
  
  .filterBox > fieldset {
    border: 5px solid rgba(0, 0, 0, 0.329);
    border-radius: 10px;
  }

  .MuiTypography-root {
    /* background:linear-gradient(0.25turn, #728d98, #1f99a2, #407a90)!important; */
    color: rgb(31, 24, 210);
  }
  
  @media screen and (max-width: 600px) {
    .filterBox {
      width: 20vmax;
      position: static;
      margin: auto;
    }
  
    .page-link {
      font: 300 1.7vmax "Roboto";
    }
    .category-link {
      font: 400 1.8vmax "Roboto";
    }
  }