.sidebar {
    /* background-color: rgb(75, 152, 160); */
    background: linear-gradient(0.25turn, #728d98, #1f99a2, #407a90);
    display: flex;
    flex-direction: column;
    padding: 4rem 0;
    border: 4px solid rgb(69, 69, 69);
  }
  
  .sidebar > a:first-child {
    padding: 0;
  }
  .sidebar > a > img {
    width: 100%;
    transition: all 0.5s;
  }
  
  .sidebar > a > img:hover {
    filter: drop-shadow(0 0 10px rgb(81, 80, 80));
  }
  .sidebar a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.493);
    font: 800 1rem "Italic";
    padding: 2rem;
    transition: all 0.5s;
  }
  .sidebar a:hover {
    color: rgb(236, 247, 29);
    transform: scale(1.1);
  }
  
  .sidebar a > P {
    display: flex;
    align-items: center;
  }
  .sidebar a > p > svg {
    margin-right: 0.5rem;
  }
  
  .sidebar.MuiTypography-root {
    background:linear-gradient(0.25turn, #728d98, #1f99a2, #407a90)!important;
  } 